@import '../../../styles/constants';

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module_CaptureView {
    img.online_capture-rectangle {
      position: absolute;
      width: 32px;
      height: 32px;
      object-fit: contain;
      display: block;

      &.online_capture-rectangle.rec-0 {
        top: $padding;
        left: $padding;
      }

      &.online_capture-frame_move.rec-0 {
        animation: rotate-rec-0 1.7s linear;

        @keyframes rotate-rec-0 {
          0% {
            top: $padding;
            left: $padding;
          }
          30% {
            top: -10px;
            left: -10px;
          }
          70% {
            top: -10px;
            left: -10px;
          }
          100% {
            top: $padding;
            left: $padding;
          }
        }
      }

      &.online_capture-rectangle.rec-90 {
        top: $padding;
        right: $padding;
        left: auto;
        transform: rotate(90deg);
      }

      &.online_capture-frame_move.rec-90 {
        animation: rotate-rec-90 1.7s linear;

        @keyframes rotate-rec-90 {
          0% {
            top: $padding;
            right: $padding;
          }
          30% {
            top: -10px;
            right: -10px;
          }
          70% {
            top: -10px;
            right: -10px;
          }
          100% {
            top: $padding;
            right: $padding;
          }
        }
      }

      &.online_capture-rectangle.rec-180 {
        bottom: $padding;
        right: $padding;
        left: auto;
        top: auto;
        transform: rotate(180deg);
      }

      &.online_capture-frame_move.rec-180 {
        animation: rotate-rec-180 1.7s linear;

        @keyframes rotate-rec-180 {
          0% {
            bottom: $padding;
            right: $padding;
          }
          30% {
            bottom: -10px;
            right: -10px;
          }
          70% {
            bottom: -10px;
            right: -10px;
          }
          100% {
            bottom: $padding;
            right: $padding;
          }
        }
      }

      &.online_capture-rectangle.rec-270 {
        bottom: $padding;
        left: $padding;
        top: auto;
        transform: rotate(270deg);
      }

      &.online_capture-frame_move.rec-270 {
        animation: rotate-rec-270 1.7s linear;

        @keyframes rotate-rec-270 {
          0% {
            bottom: $padding;
            left: $padding;
          }
          30% {
            bottom: -10px;
            left: -10px;
          }
          70% {
            bottom: -10px;
            left: -10px;
          }
          100% {
            bottom: $padding;
            left: $padding;
          }
        }
      }
    }
  }
}
