@import '../../../styles/constants';

$button_main_size: 40px;
$button_padding: 12px;

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module__Container {
    .ABBYY_Capture_Module_CaptureView {
      .ABBYY_Capture_Module_CaptureView_ButtonContainer {
        position: static;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        box-sizing: border-box;
        padding: 2 * $button_padding;

        input {
          display: none;
        }

        .ABBYY_Capture_Module_CaptureView_CentralContainer {
          display: flex;
          flex-direction: column;
          align-items: center;

          .ABBYY_Capture_Module_CaptureView_CameraContainer {
            height: 40px;
            border-radius: 8px;
            background-color: rgba(0, 0, 0, 0.5);
            padding: 0 $button_padding;
            position: relative;
            overflow: hidden;
            cursor: pointer;
            margin-bottom: 20px;
            box-sizing: content-box;
            -webkit-tap-highlight-color: transparent;

            &:hover {
              background-color: rgba(0, 0, 0, 0.6);

              .ABBYY_Capture_Module_CaptureView_CameraIcon {
                opacity: 0.8;
              }
            }

            &:active {
              background-color: rgba(0, 0, 0, 0.7);

              .ABBYY_Capture_Module_CaptureView_CameraIcon {
                opacity: 0.7;
              }
            }

            .ABBYY_Capture_Module_CaptureView_CameraPointContainer {
              position: relative;
              display: flex;
            }

            .ABBYY_Capture_Module_CaptureView_CameraPoint {
              margin: 17px $button_padding;
              width: 6px;
              height: 6px;
              border-radius: 50%;
              opacity: 0.5;
              background-color: $backgroundColor;
              flex-shrink: 0;
            }

            .ABBYY_Capture_Module_CaptureView_CameraIcon {
              position: absolute;
              width: 24px;
              height: 24px;
              top: 8px;
              left: 45px;
              background-image: url('./assets/camera.svg');

              &.yellow {
                background-image: url('./assets/cameraYellow.svg');
              }
            }

            &.cameras_max_3 {
              .ABBYY_Capture_Module_CaptureView_CameraIcon {
                transition: all 1s ease;

                &[data-position='0'] {
                  left: 15px;
                }

                &[data-position='1'] {
                  left: 45px;
                }

                &[data-position='2'] {
                  left: 75px;
                }
              }
            }

            &.cameras_multiple {
              width: 90px;

              @keyframes slide {
                0% {
                  left: 0;
                }
                100% {
                  left: -30px;
                }
              }

              @keyframes slide1 {
                0% {
                  left: 0;
                }
                100% {
                  left: -30px;
                }
              }

              .ABBYY_Capture_Module_CaptureView_CameraPointContainer {
                &.pos0 {
                  animation: slide 1s ease;
                  left: 0;
                }

                &.pos1 {
                  animation: slide1 1s ease;
                  left: 0;
                }
              }
            }
          }

          .ABBYY_Capture_Module_CaptureView__Capture_Button {
            position: relative;
            display: block;
            border: none;
            width: 48px;
            height: 48px;
            outline: none;
            pointer-events: initial;
            cursor: pointer;
            top: 2px;

            &.ABBYY_Capture_Module_CaptureView__Capture_Button--disabled {
              pointer-events: none;
              opacity: 0.3;
            }

            &:before {
              content: '';
              display: inline-block;
              width: 48px;
              height: 48px;
              background-color: white;
              border-radius: 50%;
              position: relative;
            }

            &:active:before {
              width: 44px;
              height: 44px;
              left: 2px;
              top: 2px;
            }

            &:after {
              position: absolute;
              left: -8px;
              top: -8px;
              content: '';
              display: inline-block;
              width: 52px;
              height: 52px;
              background: transparent;
              border-radius: 50%;
              border: 6px solid white;
            }
          }
        }

        .ABBYY_Capture_Module_CaptureView_Button {
          position: relative;
          width: $button_main_size;
          height: $button_main_size;
          background-color: rgba(0, 0, 0, 0.5);
          border-radius: 8px;
          outline: none;
          cursor: pointer;

          &:after {
            content: '';
            top: 8px;
            left: 8px;
            width: 24px;
            height: 24px;
            position: absolute;
            object-fit: contain;
            display: block;
            color: #ffffff;
          }

          &.ABBYY_Capture_Module_CaptureView_Button__Get_File_Button {
            &:after {
              background-image: url(./assets/ic-gallery.svg);
            }
          }

          &.ABBYY_Capture_Module_CaptureView_Button__Close_Button {
            &:after {
              background-image: url(./assets/ic-close.svg);
            }

            z-index: 1000;
          }

          &:hover {
            background-color: rgba(0, 0, 0, 0.6);

            &:after {
              opacity: 0.8;
            }
          }

          &:active {
            background-color: rgba(0, 0, 0, 0.7);

            &:after {
              opacity: 0.7;
            }
          }
        }
      }
    }
  }

  .ABBYY_Capture_Module__Container.tablet,
  .ABBYY_Capture_Module__Container.mobile.landscape {
    .ABBYY_Capture_Module_CaptureView {
      .ABBYY_Capture_Module_CaptureView_ButtonContainer {
        .ABBYY_Capture_Module_CaptureView_CentralContainer {
          flex-direction: row;
          position: absolute;
          right: 22px;
          top: calc(50% - 27px);

          .ABBYY_Capture_Module_CaptureView_CameraContainer {
            margin-bottom: 0;
            margin-right: -10px;
            transform: rotate(90deg);

            &.cameras_max_3 {
              margin-right: 0;
            }

            .ABBYY_Capture_Module_CaptureView_CameraIcon {
              transform: rotate(-90deg);
            }
          }
        }
      }
    }
  }

  .ABBYY_Capture_Module__Container.desktop {
    .ABBYY_Capture_Module_CaptureView {
      .ABBYY_Capture_Module_CaptureView_ButtonContainer {
        width: 240px;

        .ABBYY_Capture_Module_CaptureView_CentralContainer {
          .ABBYY_Capture_Module_CaptureView__Capture_Button {
            &:before {
              background-image: url('./assets/cameraGrey.svg');
              background-repeat: no-repeat;
              background-position: center;
            }

            &:hover:before {
              background-image: url('./assets/cameraGrey_hover.svg');
            }

            &:active:before {
              background-image: url('./assets/cameraGrey_active.svg');
            }
          }
        }
      }
    }
  }
}
