@import '../../../../styles/constants';

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module_CaptureView {
    .online_capture-document_title_dots {
      text-align: center;
      margin-top: 5px;

      @keyframes scale {
        0% {
          transform: scale(0);
        }
        11% {
          transform: scale(0.5);
          display: inline-block;
        }
        22% {
          transform: scale(0.75);
        }
        33% {
          transform: scale(1);
        }
        44% {
          transform: scale(0.75);
        }
        55% {
          transform: scale(0.5);
        }
        66% {
          transform: scale(0);
        }
        100% {
          transform: scale(0);
        }
      }

      span {
        width: 8px;
        height: 8px;
        margin-right: 5px;
        display: inline-block;
        animation: scale 0.9s infinite;
        -webkit-animation: scale 0.9s infinite;
        border-radius: 50%;
        background-color: $backgroundColor;

        &:nth-child(2) {
          animation-delay: 0.1s;
        }

        &:nth-child(3) {
          animation-delay: 0.2s;
        }
      }
    }
  }
}
