@import '../../../../styles/constants';

$animationSize: 34px;
$arrowSize: 24px;
$animationWidth: 10px;
$animationPadding: 4px;

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module__Container {
    .ABBYY_Capture_Module_CaptureView {
      .online_capture-document_closer-animation {
        position: absolute;

        @keyframes arrowAnimation {
          0% {
            background-position-x: $animationPadding;
          }
          50% {
            background-position-x: $animationWidth + $animationPadding;
          }
          100% {
            background-position-x: $animationPadding;
          }
        }

        &:after,
        &:before {
          border-left: 4px solid white;
          display: inline-block;
          content: '';
          width: $arrowSize + $animationWidth + $animationPadding;
          height: $arrowSize;
          position: relative;
          top: #{$animationSize/2 - $arrowSize/2};
          background-image: url('./assets/arrow.svg');
          background-repeat: no-repeat;
          animation: arrowAnimation 1.2s infinite;
        }
      }
    }
  }

  .ABBYY_Capture_Module__Container.mobile,
  .ABBYY_Capture_Module__Container.desktop,
  .ABBYY_Capture_Module__Container.desktop.landscape,
  .ABBYY_Capture_Module__Container {
    .ABBYY_Capture_Module_CaptureView {
      .online_capture-document_closer-animation {
        top: calc(50% - #{$animationSize / 2});
        left: $padding/2;
        width: calc(100% - #{$padding});
        height: $animationSize;

        &:before {
          left: 0;
          transform: none;
        }

        &:after {
          left: calc(100% - #{$padding + 24px + $arrowSize + 2 * $animationWidth});
          transform: rotate(180deg);
          top: 5px;
        }
      }
    }
  }

  .ABBYY_Capture_Module__Container.tablet,
  .ABBYY_Capture_Module__Container.landscape {
    .ABBYY_Capture_Module_CaptureView {
      .online_capture-document_closer-animation {
        top: $padding;
        left: calc(50% - #{$animationSize / 2});
        height: calc(100% - #{$padding});
        width: $animationSize;

        &:before {
          left: -$animationPadding;
          transform: rotate(90deg);
        }

        &:after {
          transform: rotate(-90deg);
          left: -$animationPadding/2;
          top: calc(100% - #{$padding + 12px + $arrowSize + 2 * $animationWidth});
        }
      }
    }
  }
}
