@import '../../styles/constants';

$svg-extraSpace: 25px;
$topPanelHeight: 54px;

:export {
  svgExtraSpace: $svg-extraSpace;
  topPanelHeight: $topPanelHeight;
}

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module__CropView {
    width: 100%;
    height: 100%;
    max-width: $maxDocumentWidth;
    display: grid;
    grid-template-rows: $topPanelHeight 1fr 56px;
    grid-template-areas: 'TopPanel' 'CropWorkingPane' 'CropNavbar';

    .ABBYY_Capture_Module__TopPanel {
      padding: $padding $svg-extraSpace;
      display: flex;
      justify-content: space-between;

      span {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        padding: 10px 0;
        text-align: center;

        &.button {
          color: $primaryColor;
          cursor: pointer;

          &:hover {
            color: #3b71b3;
          }

          &:active {
            color: #33639d;
          }
        }

        &.normal {
          font-weight: normal;
        }

        &:last-child {
          text-align: end;
        }

        &:first-child {
          text-align: start;
        }
      }

      span:active:not(.button) {
        opacity: 1;
      }
    }

    .ABBYY_Capture_Module__CropView_WorkingPane {
      flex-grow: 1;
      overflow: hidden;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      max-width: calc(#{$maxDocumentWidth} - 2 * #{$svg-extraSpace});
      grid-area: CropWorkingPane;
      box-sizing: border-box;
      grid-row-start: 1;
      padding: $topPanelHeight $svg-extraSpace $svg-extraSpace;

      & > div {
        position: relative;
        visibility: hidden; // will change to visible after resize

        img {
          display: block;
          max-width: 100%;
          max-height: inherit;
        }

        .ABBYY_Capture_Module__CropView_CropLayer {
          margin: -$svg-extraSpace;
          position: absolute;
          left: 0;
          top: 0;
          width: calc(100% + 2 * #{$svg-extraSpace});
          height: calc(100% + 2 * #{$svg-extraSpace});
        }
      }
    }

    .ABBYY_Capture_Module__CropView_BottomPane {
      grid-area: CropNavbar;
      width: 100%;
      padding: $padding/2 ($svg-extraSpace - $padding/2);
      color: #ffffff;
      box-sizing: border-box;
      display: flex;
      justify-content: space-between;

      @mixin cropButton {
        text-align: center;
        border: 0;
        outline: none;
        width: 40px;
        height: 40px;
        background-color: transparent;
        background-position: center;
        background-repeat: no-repeat;
      }

      .ABBYY_Capture_Module__CropView_FullCrop {
        @include cropButton();
        background-image: url('./assets/crop-full.svg');
      }

      .ABBYY_Capture_Module__CropView_OriginalCrop {
        @include cropButton();
        background-image: url('./assets/crop-original.svg');
      }

      .ABBYY_Capture_Module__Navbar_pager {
        color: $secondaryColor;
      }

      .ABBYY_Capture_Module__Navbar_prev,
      .ABBYY_Capture_Module__Navbar_next {
        color: $primaryColor;
        width: 40px;
        height: 40px;
        background: $backgroundColor;

        &:after {
          top: $padding/2;
          left: $padding/2;
        }

        &:hover:after,
        &:active:after {
          opacity: 1;
        }
      }

      .ABBYY_Capture_Module__Navbar_prev {
        &:after {
          background-image: url('./assets/ic-back.svg');
        }
      }

      .ABBYY_Capture_Module__Navbar_next {
        &:after {
          background-image: url('./assets/ic-next.svg');
        }
      }
    }
  }

  .ABBYY_Capture_Module__MessagePage {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module__Container.desktop {
    .ABBYY_Capture_Module__CropView {
      .ABBYY_Capture_Module__CropView_BottomPane {
        .ABBYY_Capture_Module__CropView_FullCrop {
          &:hover {
            background-image: url('./assets/crop-full_hover.svg');
          }

          &:active {
            background-image: url('./assets/crop-full_active.svg');
          }
        }

        .ABBYY_Capture_Module__CropView_OriginalCrop {
          &:hover {
            background-image: url('./assets/crop-original_hover.svg');
          }

          &:active {
            background-image: url('./assets/crop-original_active.svg');
          }
        }

        .ABBYY_Capture_Module__Navbar_prev {
          &:hover:after {
            background-image: url('./assets/ic-back_hover.svg');
          }

          &:active:after {
            background-image: url('./assets/ic-back_active.svg');
          }
        }

        .ABBYY_Capture_Module__Navbar_next {
          &:hover:after {
            background-image: url('./assets/ic-next_hover.svg');
          }

          &:active:after {
            background-image: url('./assets/ic-next_active.svg');
          }
        }
      }
    }
  }
}
