@import '../../styles/constants';

@font-face {
  font-family: 'SourceSansPro';
  src: url('../../assets/fonts/SourceSansPro-Regular.woff2') format('woff2');
}

@font-face {
  font-family: 'SourceSansPro';
  src: url('../../assets/fonts/SourceSansPro-Semibold.woff2') format('woff2');
  font-weight: 600;
}

body {
  overscroll-behavior: none;
}

.ABBYY_Capture_Module {
  background: $backgroundColor;
  position: fixed;
  font-size: 17px;
  font-weight: 600;
  font-family: SourceSansPro, Arial, sans-serif;
  z-index: 9999;
  user-select: none;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;

  .ABBYY_Capture_Module__Container {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;
  }

  /** global styles */
  button {
    cursor: pointer;
    font-size: 17px;
    font-family: SourceSansPro, Arial, sans-serif;
    font-weight: 600;
    padding: 0;

    &:disabled {
      cursor: default;
      opacity: 0.6;
    }
  }

  img {
    user-select: all;
  }
}
