@import '../../../styles/constants';

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module_CaptureView {
    $padding: 8px;

    .online_capture-document {
      display: flex;
      justify-content: center;
      align-items: center;

      .online_capture-document_title {
        padding: $padding 16px;
        border-radius: 14pt;
        background-color: rgba(33, 33, 33, 0.72);
        font-weight: normal;
        color: $backgroundColor;
        text-align: center;
      }
    }
  }
}
