@import '../../styles/constants';

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module_ImagePreview {
    display: grid;
    grid-template-rows: 64px 1fr $paneSize 68px;
    grid-template-columns: 0 1fr 0;
    grid-template-areas: '. TopPanel .' 'NavbarPrev ImagePreview NavbarNext' '. Navbar .' '. BottomPanel .';
    width: 100%;
    height: 100%;

    @media only screen and (min-width: $TabletWidthThreshold - 1) {
      grid-template-rows: 116px 1fr 108px;
      grid-template-columns: 0 1fr 2fr 0;
      grid-template-areas: '. Navbar BottomPanel .' 'NavbarPrev ImagePreview ImagePreview NavbarNext' '. TopPanel TopPanel .';
    }

    @media only screen and (min-width: $maxDocumentWidth + 1) {
      grid-template-columns: minmax(0, 1fr) 350px 675px minmax(0, 1fr);
    }

    .ABBYY_Capture_Module_ImageParentContainer {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0 $padding;
      position: relative;
      grid-area: ImagePreview;

      .ABBYY_Capture_Module_ImageContainer {
        display: block;

        img {
          display: block;
          max-width: 100%;
          max-height: inherit;
          object-fit: contain;
          border: solid 1px rgba(0, 0, 0, 0.1);
        }
      }

      .ABBYY_Capture_Module-Show_Warnings_Button {
        position: absolute;
        width: 40px;
        height: 40px;
        left: calc(50% - 20px);
        border-radius: 8px;
        background-color: rgba(0, 0, 0, 0.8);
        bottom: 20px;

        &:after {
          position: absolute;
          margin: 8px;
          content: '';
          width: 24px;
          height: 24px;
          object-fit: contain;
          background-image: url(./warning.svg);
        }
      }
    }

    .ABBYY_Capture_Module__MessagePage {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
    }
  }
}
