@import '../../styles/constants';

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module__Navbar_pager {
    display: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    grid-area: Navbar;
    text-overflow: ellipsis;

    @media only screen and (min-width: $TabletWidthThreshold - 1) {
      justify-content: flex-start;
      padding-left: 2 * $padding;
    }
  }

  .ABBYY_Capture_Module__Navbar_prev,
  .ABBYY_Capture_Module__Navbar_next {
    width: $paneSize;
    height: $paneSize;
    position: relative;
    background-color: rgba(0, 0, 0, 0.4);
    cursor: pointer;
    align-self: center;

    &:hover {
      background-color: rgba(0, 0, 0, 0.5);

      &:after {
        opacity: 0.8;
      }
    }

    &:active {
      background-color: rgba(0, 0, 0, 0.6);

      &:after {
        opacity: 0.7;
      }
    }

    &:after {
      content: '';
      position: absolute;
      top: 12px;
      left: 16px;
      width: 24px;
      height: 24px;
      object-fit: contain;
      display: block;
    }

    &.ABBYY_Capture_Module__Navbar_next--disabled,
    &.ABBYY_Capture_Module__Navbar_prev--disabled {
      opacity: 0.3;
      cursor: default;
    }
  }

  .ABBYY_Capture_Module__Navbar_prev {
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    grid-area: NavbarPrev;

    &:after {
      background-image: url(./ic-back.svg);
    }
  }

  .ABBYY_Capture_Module__Navbar_next {
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
    grid-area: NavbarNext;
    justify-self: end;

    &:after {
      background-image: url(./ic-next.svg);
    }
  }
}
