.ABBYY_Capture_Module {
  .ABBYY_Capture_Module__TopPanel {
    .ABBYY_Capture_Module__ToolBar_button {
      font-size: 14px;
      padding: 26px 8px 0 8px;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 25%;
      cursor: pointer;
      color: #4a8ee0;
      text-align: center;
      position: relative;

      &.ABBYY_Capture_Module__ToolBar_button--disabled {
        opacity: 0.3;
      }

      &:before {
        content: '';
        width: 24px;
        height: 24px;
        position: absolute;
        top: 0;
        left: calc(50% - 12px);
      }

      &.ABBYY_Capture_Module__ToolBar_button_img--delete {
        &:before {
          background-image: url(./assets/ic-del.svg);
        }
      }

      &.ABBYY_Capture_Module__ToolBar_button_img--crop {
        &:before {
          background-image: url(./assets/ic-crop.svg);
        }
      }

      &.ABBYY_Capture_Module__ToolBar_button_img--retake {
        &:before {
          background-image: url(./assets/ic-reshoot.svg);
        }
      }

      &.ABBYY_Capture_Module__ToolBar_button_img--rotate {
        &:before {
          background-image: url(./assets/ic-rotate.svg);
        }
      }
    }
  }
}

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module__Container.desktop {
    .ABBYY_Capture_Module__TopPanel {
      .ABBYY_Capture_Module__ToolBar_button {
        &:hover {
          color: #3b71b3;
        }

        &:active {
          color: #33639c;
        }

        &.ABBYY_Capture_Module__ToolBar_button_img--delete {
          &:hover:before {
            background-image: url('./assets/ic-del_hover.svg');
          }

          &:active:before {
            background-image: url('./assets/ic-del_active.svg');
          }
        }

        &.ABBYY_Capture_Module__ToolBar_button_img--crop {
          &:hover:before {
            background-image: url('./assets/ic-crop_hover.svg');
          }

          &:active:before {
            background-image: url('./assets/ic-crop_active.svg');
          }
        }

        &.ABBYY_Capture_Module__ToolBar_button_img--retake {
          &:hover:before {
            background-image: url('./assets/ic-reshoot_hover.svg');
          }

          &:active:before {
            background-image: url('./assets/ic-reshoot_active.svg');
          }
        }

        &.ABBYY_Capture_Module__ToolBar_button_img--rotate {
          &:hover:before {
            background-image: url('./assets/ic-rotate_hover.svg');
          }

          &:active:before {
            background-image: url('./assets/ic-rotate_active.svg');
          }
        }
      }
    }
  }
}
