$magnifier-size: 64px;

:export {
  magnifierSize: $magnifier-size;
}

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module__CropView {
    .ABBYY_Capture_Module__CropView_Magnifier {
      display: none;
      position: absolute;
      width: $magnifier-size;
      height: $magnifier-size;
      box-shadow: 0 12px 12px 0 rgba(0, 0, 0, 0.2);
      border-radius: 50%;
      border: 1px solid #ffdc48;
      left: 0;
      top: 0;
      background-repeat: no-repeat;
      background-color: white;

      canvas {
        border-radius: 50%;
      }
    }
  }
}
