@import '../../../styles/constants';

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module_BottomPanel {
    display: flex;
    justify-content: space-between;
    padding: 0 $padding;
    grid-area: BottomPanel;

    @media only screen and (min-width: $TabletWidthThreshold - 1) {
      justify-content: flex-end;
      padding: 0 2 * $padding;
      align-items: center;
    }

    .ABBYY_Capture_Module_BottomPanel_button {
      height: $paneSize;
      border-radius: 8px;
      border: none;
      background-color: #eef2f4;
      width: 100%;
      text-align: center;
      color: #424650;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;

      &:hover {
        box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.2);
      }

      &:active {
        background-color: #bfc2c3;
      }

      @media only screen and (min-width: $TabletWidthThreshold - 1) {
        width: 136px;
      }

      &:first-child {
        margin-right: 16px;
      }

      &.ABBYY_Capture_Module_BottomPanel_button--save {
        background-color: #4a8ee0;
        color: #ffffff;

        &:active {
          background-color: #3b72b3;
        }
      }

      &:disabled {
        opacity: 0.3;
      }
    }
  }
}
